import { observer } from "mobx-react-lite";
import React, { useEffect, useState } from "react";
import { Button, List, Segment } from "semantic-ui-react";
import { Invoice } from "../../../../app/models/invoice";
import { useStore } from "../../../../app/stores/store";

export default observer(function InvoicesList() {
  const { ordersStore } = useStore();
  const [invoices, setInvoices] = useState<Invoice[]>([]);

  useEffect(() => {
    async function GetList() {
      const temp = await ordersStore.getInvoicesList();
      if (temp) {
        setInvoices(temp);
      }
    }

    GetList();
  }, [ordersStore]);

  return (
    <Segment padded piled raised>
      <List divided relaxed>
        {invoices &&
          invoices.map((invoice) => (
            <List.Item>
              <List.Icon name="copy outline" />
              <List.Content>
                <List.Header>
                  Faktura: {invoice.address.street} {invoice.address.zipCode} {invoice.address.city} | {invoice.nip}
                  <Button
                    icon="arrow alternate circle down outline"
                    circular
                    style={{ margin: 0, padding: 2, marginLeft: 5 }}
                    onClick={()=>{
                    ordersStore.getInvoice(invoice.id);  
                    }}
                  />
                  <Button 
                  icon='remove'
                  floated='right'
                  color='red'
                  />
                </List.Header>
                <List.Description>{invoice.firstName} {invoice.lastName}</List.Description>
              </List.Content>
              
            </List.Item>
          ))}
      </List>
    </Segment>
  );
});
